header{
    background-color: $color_destaque;
    padding: 10px 0;
    display: inline-flex;
    width: 100%;
    .content{
        display: inline-flex;
        flex-wrap:wrap;
        align-items:center;
        justify-content:center;
        @include desktop{
            justify-content: space-between;
        }
        .logo{ 
            
             width: 100%;
            text-align: center;
            margin-bottom: 15 px;
            @include desktop{
                width: auto;
                margin-bottom: 0 px;
            }
            img{
                display: block;
                margin: 0 auto;
            }
        }
        .login{
            form{
                @include desktop{
                    display: inline-flex;
                    flex-wrap: wrap;
                }
                input{
                    width: 100%;
                    margin: 0 0 15px 0;
                    
                    @include desktop{
                        max-width: 205px;
                        margin-bottom: 0 px;
                        margin-right: 15px;

                    }

                }
                input[type="submit"]{
                    margin: 0 auto;
                    width: 100%;

                @include desktop{
                    margin: 0;
                    width: auto;
                }
                }
            }
        }
    }
}